  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

  html {
      overflow-x: hidden;
  }

  * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      user-select: none;
  }

  body {
      height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      background: #f3f5fc;
  }

  #main-wrapper {
      width: 600px;
      height: 420px;
      background: white;
      padding: 90px 0;
      border-radius: 18px;
      border: 1px solid #e7ecfb;
      box-shadow: 0 0 30px #e7ecfb;
      display: flex;
      align-items: center;
      justify-content: center;
  }

  #start-wrapper {
      text-align: center;
  }




  .auto-typin {
      width: 22ch;
      animation: typing 2s steps(22), blink .5s step-end infinite alternate;
      white-space: nowrap;
      overflow: hidden;
      border-right: 3px solid;
      font-family: monospace;
      font-size: 2em;
  }

  @keyframes typing {
      from {
          width: 0
      }
  }

  @keyframes blink {
      50% {
          border-color: transparent
      }
  }

  #start-title {
      font-weight: bold;
      color: #666f99;
      font-size: 42px;
  }

  #start-title span {
      font-size: 0.8em;
  }

  #start-username-wrapper {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
  }

  #username {
      margin: 48px 0;
      background: #f3f5fc;
      padding: 12px 18px;
      border-radius: 50px;
      border: 1px solid #e7ecfb;
      text-align: center;
      font-size: 16px;
      color: #666f99;
      transition: enterUsername, background-color 300ms ease-out;
  }

  #username:focus,
  .btn:focus,
  input:focus,
  #sort-btn:focus {
      outline: 1px dashed #666f99;
  }

  #username-placeholder {
      color: #bcc0d2;
      font-weight: 400;
      position: absolute;
      pointer-events: none;
      transition: 300ms ease-out;
  }

  .btn {
      padding: 9px 18px;
      background: #666f99;
      border: 0;
      border-radius: 3px;
      font-size: 16px;
      font-weight: 600;
      color: white;
      letter-spacing: 1px;
  }

  .btn:hover {
      background: #565e81;
  }

  .btn:active {
      background: #414762;
  }

  #quiz-wrapper {
      margin: 90px 0;
      padding: 0 30px;
      width: 100%;
      height: 100%;
  }

  .hidden_quizz {
      display: none;
  }

  #progress-wrapper {
      position: relative;
      top: -60px;
      padding: 0 30px;
      margin-top: 60px;
  }

  #progress-icons-wrapper {
      display: flex;
      justify-content: space-between;
  }

  .fas,
  .fab {
      font-size: 30px;
  }

  #progress-html,
  #progress-css,
  #progress-js,
  #progress-end {
      width: 60px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: #fafcf3;
      border: 1px solid #e7ecfb;
      position: relative;
      z-index: 2;
      transition: 300ms ease-out;
  }

  #progress-html {
      background: #e7ecfb;
      border-color: #d3dcf8;
  }

  .fa-html5 {
      color: #f16529;
  }

  .fa-css3-alt {
      color: #2aa8e0;
  }

  .fa-js-square {
      color: #f8be01;
  }

  #progress-bar {
      height: 20px;
      width: 0;
      background: #e7ecfb;
      position: absolute;
      top: 21px;
      left: 33px;
      z-index: 1;
      transition: 300ms ease-out;
  }

  #progress-bar-uncompleted {
      height: 20px;
      width: calc(100% - 66px);
      border-top: 1px solid #e7ecfb;
      border-bottom: 1px solid #e7ecfb;
      position: absolute;
      top: 21px;
      left: 33px;
      z-index: 1;
  }

  .fa-flag-checkered {
      color: #666f99;
  }

  #question-wrapper {
      text-align: center;
      margin-top: -30px;
      width: 100%;
  }

  #question {
      font-size: 18px;
      color: #666f99;
      width: 100%;
  }

  #answers-wrapper {
      margin: 24px 0;
      display: flex;
      flex-direction: column;
      align-items: center;
  }

  .answer-wrapper {
      margin: 6px 0;
      display: flex;
      align-items: center;
      position: relative;
  }

  .checkmark {
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      background: #e7ecfb;
      margin-left: -12px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: 300ms ease-out;
      pointer-events: none;
  }

  .checkmark::after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: white;
      opacity: 0;
  }

  .answer-wrapper:hover .checkmark {
      background: #d3dcf8;
  }

  .answer-wrapper input:checked~.checkmark {
      background: #666f99;
  }

  .answer-wrapper input:checked~.checkmark::after {
      opacity: 1;
  }

  .answer-input {
      position: relative;
      left: -8px;
      transform: scale(1.4);
  }

  input[type="radio"] {
      margin: 9px 0;
      opacity: 0;
  }

  .answer {
      color: #666f99;
  }

  #next-btn-wrapper,
  #start-btn-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
  }

  #leaderboard-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  }

  #leaderboard-title,
  table,
  tr {
      text-align: center;
      color: #666f99;
  }

  #retry-btn-wrapper {
      text-align: center;
  }

  table {
      width: 180px;
      -webkit-border-vertical-spacing: 12px;
      margin: 30px 0;
  }

  #table-username-title,
  .table-username {
      position: relative;
      left: -15px;
  }

  #sort-btn {
      background: none;
      border: none;
      font-size: 16px;
      font-weight: bold;
      color: #666f99;
      padding: 9px;
  }

  #sort-btn:hover .fa-arrows-alt-v {
      animation: sortArrowEnter 300ms ease-out;
  }

  .fa-arrows-alt-v {
      position: relative;
      left: 1px;
      top: -1px;
      font-size: 16px;
      transition: 300ms ease-out;
  }

  #quiz-wrapper,
  #leaderboard-wrapper {
      display: none;
  }

  @keyframes enterUsername {
      0% {
          transform: translateX(-10px);
      }

      20% {
          transform: translateX(10px);
      }

      40% {
          transform: translateX(-10px);
      }

      60% {
          transform: translateX(5px);
      }

      80% {
          transform: translateX(-5px);
      }

      100% {
          transform: translateX(0);
      }
  }

  @keyframes sortArrowEnter {
      0% {
          transform: rotateZ(0);
      }

      100% {
          transform: rotateZ(180deg);
      }
  }

  @keyframes progressSection {
      0% {
          transform: scale(1);
      }

      50% {
          transform: scale(1.2);
      }

      100% {
          transform: scale(1);
      }
  }

  @media (max-width: 720px) {
      body {
          background: white;
      }

      #main-wrapper {
          height: 100vh;
          padding: 0 120px;
          border: 0;
          box-shadow: none;
      }

      #progress-wrapper {
          padding: 0;
      }

      #progress-html,
      #progress-css,
      #progress-js,
      #progress-end {
          width: 42px;
          height: 42px;
      }

      #progress-bar {
          top: 12px;
      }

      #progress-bar-uncompleted {
          top: 12px;
      }

      .fas,
      .fab {
          font-size: 24px;
      }

      #start-title {
          font-size: 30px;
      }

      #question {
          font-size: 18px;
      }
  }

  @media all and (-ms-high-contrast: none),
  (-ms-high-contrast: active) {
      .checkmark {
          top: 5px;
          left: -1px;
      }

      .checkmark::after {
          left: 6px;
          top: 6px;
      }

      #username-placeholder {
          left: 110px;
          top: 60px;
      }
  }